body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.page-header {
  background-color: white;
  width: 100%;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  display: block; }
  .page-header .main-menu {
    height: 4.375rem; }

.left-logo {
  width: auto;
  height: 2rem;
  vertical-align: middle;
  border-style: none; }

@media (max-width: 768px) {
  .hidden-lg {
    display: none !important; } }

.item_big {
  text-align: left;
  margin: 0;
  padding: 0;
  list-style: none outside; }
  .item_big .nav-item {
    display: inline-block;
    float: none;
    position: relative;
    margin-right: 0px;
    margin-left: 1px; }

.section-header {
  display: block;
  width: 100%;
  background: #fff;
  overflow: hidden;
  border-bottom: 1px solid #ebebeb;
  margin-bottom: 40px; }

.bread-crumb {
  background-color: #fff;
  list-style: none;
  margin: 0;
  font-size: 14px;
  padding: 15px 0;
  border-radius: 0; }
  .bread-crumb li {
    display: inline; }
    .bread-crumb li a {
      color: #333;
      text-decoration: none;
      background: transparent; }
    .bread-crumb li strong {
      color: #008744;
      font-weight: 400; }

.footer-section {
  display: block;
  background-color: #212121;
  color: white;
  margin-top: 6rem;
  padding: 48px 0px 20px; }

.list-circles {
  display: block;
  width: 28px;
  font-size: 16px;
  line-height: 18px;
  color: white;
  text-align: center;
  padding: 4px;
  border-width: 1px;
  border-style: solid;
  border-color: white;
  border-image: none;
  border-image: initial;
  border-radius: 14px; }
  .list-circles svg {
    width: 16px;
    height: 16px;
    fill: white; }

.upper {
  border-bottom: 1px solid rgba(255, 255, 255, 0.11);
  padding-bottom: 24px; }

.logo img {
  height: 3.5rem;
  width: 10rem; }

.gov-link {
  display: inline-block;
  width: 88px;
  margin-right: 15px;
  margin-bottom: 10px; }
  .gov-link img {
    width: 100%; }
  .gov-link span {
    white-space: nowrap; }

@media (min-width: 768px) {
  .hidden-xs {
    display: none !important; } }

/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 30px;
  left: 36px;
  top: 36px; }

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: #373a47; }

/* Color/shape of burger icon bars on hover*/
.bm-burger-bars-hover {
  background: #a90000; }

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px; }

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7; }

/*
  Sidebar wrapper styles
  Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
  */
.bm-menu-wrap {
  position: fixed;
  height: 100%; }

/* General sidebar styles */
.bm-menu {
  background: #373a47;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em; }

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47; }

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
  padding: 0.8em; }

/* Individual item */
.bm-item {
  display: inline-block; }

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3); }

.button-item {
  border: 0 !important; }
  .button-item :focus {
    outline: none;
    outline: 5px auto -webkit-focus-ring-color;
    outline-offset: -2px;
    outline: none; }

.title-filter svg {
  -webkit-align-self: center;
          align-self: center; }

